/*!
 * Power Saver - Bluetooth Power Meter Web App
 * Main Stylesheet
 * 
 * Table of Contents:
 * 1. Base Styles & Reset
 * 2. Layout & Grid System  
 * 3. Components (Cards, Buttons, etc.)
 * 4. UI Elements (Menu, Modals, etc.)
 * 5. Responsive Design
 * 6. Animations & Transitions
 * 7. Accessibility & Focus States
 */
:root {
  --bg-primary: linear-gradient(135deg, #0c0c0c 0%, #1a1a1a 50%, #0c0c0c 100%);
  --bg-secondary: linear-gradient(135deg, #1e1e1e 0%, #2a2a2a 100%);
  --bg-card: linear-gradient(135deg, #1e1e1e 0%, #2a2a2a 100%);
  --bg-additional-card: linear-gradient(135deg, #1a1a1a 0%, #252525 100%);
  --text-primary: #fff;
  --text-secondary: #999;
  --border-color: #ffffff1a;
  --shadow-color: #0006;
  --accent-power: #f39c12;
  --accent-hr: #e74c3c;
  --accent-cadence: #3498db;
  --accent-speed: #2ecc71;
}

* {
  box-sizing: border-box;
}

body {
  background: var(--bg-primary);
  min-height: 100vh;
  color: var(--text-primary);
  overscroll-behavior: contain;
  overscroll-behavior-y: contain;
  margin: 0;
  padding: 20px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica, Arial, sans-serif;
  transition: background .3s, color .3s;
}

.dashboard {
  flex-direction: column;
  gap: 20px;
  max-width: 1400px;
  margin: 0 auto;
  padding: 0;
  display: flex;
}

.metric-card {
  background: var(--bg-card);
  box-shadow: 0 10px 40px var(--shadow-color);
  border: 1px solid var(--border-color);
  text-align: center;
  border-radius: 24px;
  flex-direction: column;
  align-items: center;
  padding: 2rem;
  transition: all .3s;
  display: flex;
  position: relative;
  overflow: hidden;
}

.metric-card:hover {
  box-shadow: 0 15px 50px var(--shadow-color);
  transform: translateY(-2px);
}

.metric-display {
  justify-content: center;
  align-items: center;
  gap: 1rem;
  margin-bottom: 1rem;
  display: flex;
}

.metric-connect-button {
  background: linear-gradient(135deg, var(--accent-color, #f39c12) 0%, var(--accent-color, #f39c12) 100%);
  color: #1a1a1a;
  cursor: pointer;
  border: none;
  border-radius: 12px;
  flex-shrink: 0;
  align-items: center;
  gap: 8px;
  padding: 8px 16px;
  font-size: .9rem;
  font-weight: 700;
  line-height: 1;
  transition: all .3s;
  display: flex;
  box-shadow: 0 4px 15px #0000004d;
}

.metric-connect-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px #0006;
}

.metric-connect-button:active {
  transform: translateY(0);
}

.metric-connect-button .emoji {
  font-size: 1.5rem;
  line-height: 1;
}

.metric-connect-button .connect-text {
  font-size: .9rem;
  font-weight: 700;
}

.metric-connect-button[data-connected="true"] .connect-text {
  display: none;
}

.metric-connect-button[data-connected="true"] {
  padding: 8px 12px;
}

.metric-connect-button[data-connected="false"] ~ .metric-value, .metric-connect-button[data-connected="false"] ~ .metric-unit {
  display: none !important;
}

.metric-icon-button {
  cursor: pointer;
  background: none;
  border: none;
  flex-shrink: 0;
  margin: 0;
  padding: 0;
  font-size: 2rem;
  line-height: 1;
  transition: all .3s;
}

.metric-icon-button:hover {
  transform: scale(1.1);
}

.metric-icon-button:active {
  transform: scale(.95);
}

.metric-unit {
  color: var(--text-secondary);
  text-transform: lowercase;
  letter-spacing: .5px;
  flex-shrink: 0;
  font-size: 1rem;
  font-weight: 600;
}

.metric-value {
  color: var(--accent-color, #fff);
  text-shadow: 0 4px 20px #00000080;
  letter-spacing: -.02em;
  font-variant-numeric: tabular-nums;
  font-size: clamp(6rem, 12vw, 10rem);
  font-weight: 900;
  line-height: 1;
}

.metrics-row {
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  display: grid;
}

.metrics-row .metric-card .metric-value {
  font-size: clamp(4rem, 8vw, 6rem);
}

.metrics-row .metric-card .metric-connect-button .emoji {
  font-size: 1.2rem;
}

.metrics-row .metric-card .metric-connect-button .connect-text {
  font-size: .8rem;
}

.metrics-row .metric-card .metric-unit {
  font-size: .85rem;
}

.power-card {
  --accent-color: var(--accent-power);
}

.hr-card {
  --accent-color: var(--accent-hr);
}

.cadence-card {
  --accent-color: var(--accent-cadence);
}

.header {
  text-align: center;
  background: var(--bg-secondary);
  box-shadow: 0 8px 32px var(--shadow-color);
  border: 1px solid var(--border-color);
  border-radius: 20px;
  padding: 2rem;
}

.metric-icon {
  border: 2px solid var(--accent-color, #f39c12);
  cursor: pointer;
  backdrop-filter: blur(10px);
  background: #ffffff14;
  border-radius: 12px;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 48px;
  height: 48px;
  padding: 0;
  font-size: 26px;
  transition: all .3s;
  display: flex;
}

.metric-icon:hover {
  background: #ffffff26;
  transform: scale(1.08);
  box-shadow: 0 6px 20px #0006;
}

.metric-icon:active {
  transform: scale(.95);
}

.connect-button {
  background: linear-gradient(135deg, var(--accent-color, #f39c12) 0%, var(--accent-color, #f39c12) 100%);
  color: #1a1a1a;
  cursor: pointer;
  border: none;
  border-radius: 12px;
  flex-direction: column;
  align-items: center;
  gap: 4px;
  width: 100%;
  padding: 12px 20px;
  font-size: .9rem;
  font-weight: 600;
  transition: all .3s;
  display: flex;
  position: relative;
  overflow: hidden;
  box-shadow: 0 4px 15px #0000004d;
}

.connect-button:before {
  content: "";
  background: linear-gradient(90deg, #0000, #ffffff40, #0000);
  width: 100%;
  height: 100%;
  transition: left .5s;
  position: absolute;
  top: 0;
  left: -100%;
}

.connect-button:hover:before {
  left: 100%;
}

.connect-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px #0006;
}

.connect-button:active {
  transform: translateY(0);
}

.connect-button:disabled {
  color: var(--text-secondary);
  cursor: not-allowed;
  box-shadow: none;
  background: #444;
  transform: translateY(0);
}

.connect-button:disabled:before {
  display: none;
}

.button-text {
  font-size: .9rem;
  font-weight: 600;
}

.connection-info {
  opacity: .85;
  flex-direction: column;
  align-items: center;
  gap: 2px;
  font-size: .7rem;
  display: flex;
}

.device-name {
  color: currentColor;
  font-size: .7rem;
  font-weight: 600;
  display: none;
}

.status-text {
  color: currentColor;
  font-size: .65rem;
  font-style: italic;
  display: none;
}

.metric-card {
  background: var(--bg-card);
  box-shadow: 0 8px 32px var(--shadow-color);
  border: 1px solid var(--border-color);
  text-align: center;
  border-radius: 20px;
  padding: .5rem;
  transition: all .3s;
  position: relative;
  overflow: hidden;
}

.metric-card:before {
  content: "";
  background: linear-gradient(90deg, var(--accent-color, #f39c12) 0%, transparent 100%);
  height: 3px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.controls-container {
  background: var(--bg-secondary);
  box-shadow: 0 8px 32px var(--shadow-color);
  border: 1px solid var(--border-color);
  text-align: center;
  border-radius: 20px;
  padding: 2rem;
}

h1 {
  color: var(--accent-power);
  background: linear-gradient(45deg, var(--accent-power), var(--accent-power));
  -webkit-text-fill-color: transparent;
  -moz-text-fill-color: transparent;
  background-clip: text;
  margin: 0 0 1rem;
  font-size: 2.5rem;
  font-weight: 700;
}

.metric-value {
  color: var(--accent-color, #fff);
  text-shadow: 0 2px 10px #00000080;
  text-align: center;
  margin-bottom: .1rem;
  font-size: clamp(4rem, 8vw, 6rem);
  font-weight: 1000;
  line-height: 1;
  transition: transform .2s, color .3s;
}

@keyframes pulse {
  0%, 100% {
    opacity: 1;
  }

  50% {
    opacity: .6;
  }
}

#power-value {
  color: var(--accent-power);
  filter: drop-shadow(0 0 20px #f39c1266);
  display: inline-block;
  position: relative;
}

.power-card .metric-value {
  letter-spacing: -.02em;
  font-size: clamp(7rem, 18vw, 14rem);
  font-weight: 1000;
}

.metric-label {
  color: var(--text-secondary);
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: .5rem;
  font-weight: 600;
  line-height: 1.2;
}

.metric-row {
  justify-content: center;
  align-items: center;
  gap: .8rem;
  display: flex;
}

.metric-info {
  flex-direction: column;
  gap: .1rem;
  display: flex;
}

.metric-unit {
  color: var(--text-secondary);
  text-transform: uppercase;
  letter-spacing: .5px;
  font-size: .4rem;
  font-weight: 500;
}

.device-name {
  color: var(--accent-color, var(--accent-power));
  margin-top: .5rem;
  font-size: .9rem;
  font-weight: 600;
  display: none;
}

.status-text {
  color: var(--text-secondary);
  margin-top: 1rem;
  font-size: .9rem;
  font-style: italic;
  display: none;
}

button {
  color: var(--bg-primary);
  cursor: pointer;
  background: linear-gradient(135deg, #f39c12 0%, #e67e22 100%);
  border: none;
  border-radius: 12px;
  margin: 8px;
  padding: 16px 32px;
  font-size: 16px;
  font-weight: 600;
  transition: all .3s;
  position: relative;
  overflow: hidden;
  box-shadow: 0 4px 16px #f39c124d;
}

button:before {
  content: "";
  background: linear-gradient(90deg, #0000, #fff3, #0000);
  width: 100%;
  height: 100%;
  transition: left .5s;
  position: absolute;
  top: 0;
  left: -100%;
}

button:hover:before {
  left: 100%;
}

button:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 24px #f39c1266;
}

button:disabled {
  color: var(--text-secondary);
  cursor: not-allowed;
  box-shadow: none;
  background: #555;
  transform: translateY(0);
}

button:disabled:before {
  display: none;
}

.metric-connect-button {
  background: linear-gradient(135deg, var(--accent-color, #f39c12) 0%, var(--accent-color, #f39c12) 100%);
  color: var(--bg-primary);
  cursor: pointer;
  opacity: .9;
  border: none;
  border-radius: 8px;
  width: 100%;
  margin: 8px 0 0;
  padding: 8px 16px;
  font-size: .75rem;
  font-weight: 600;
  transition: all .3s;
  position: relative;
  overflow: hidden;
  box-shadow: 0 2px 8px #0003;
}

.metric-connect-button:before {
  content: "";
  background: linear-gradient(90deg, #0000, #fff3, #0000);
  width: 100%;
  height: 100%;
  transition: left .5s;
  position: absolute;
  top: 0;
  left: -100%;
}

.metric-connect-button:hover:before {
  left: 100%;
}

.metric-connect-button:hover {
  opacity: 1;
  transform: translateY(-1px);
  box-shadow: 0 4px 12px #0000004d;
}

.metric-connect-button:disabled {
  color: var(--text-secondary);
  cursor: not-allowed;
  box-shadow: none;
  opacity: .6;
  background: #555;
  transform: translateY(0);
}

.metric-connect-button:disabled:before {
  display: none;
}

.metric-connect-button .status-text, .metric-connect-button .device-name {
  opacity: .8;
  margin-top: 2px;
  font-size: .6rem;
  line-height: 1.2;
}

.export-buttons {
  flex-wrap: wrap;
  justify-content: center;
  gap: 12px;
  margin-top: 1.5rem;
  display: flex;
}

.export-buttons button {
  background: linear-gradient(135deg, #3498db 0%, #2980b9 100%);
  box-shadow: 0 4px 16px #3498db4d;
}

.export-buttons button:hover {
  box-shadow: 0 8px 24px #3498db66;
}

.config-notice {
  background: #ffb74d1a;
  border: 1px solid #ffb74d4d;
  border-radius: 8px;
  margin-top: 1rem;
  padding: .75rem;
  font-size: .85rem;
}

.config-notice code {
  white-space: pre-line;
  background: #0006;
  border-radius: 4px;
  margin-top: .5rem;
  padding: .5rem;
  font-family: Courier New, monospace;
  font-size: .8rem;
  line-height: 1.4;
  display: block;
}

#clearSessionButton {
  background: #f44336;
  margin-top: 1rem;
}

.toggle-controls {
  text-align: center;
  margin: 1rem 0;
}

.toggle-button {
  background: linear-gradient(135deg, #9b59b6 0%, #8e44ad 100%);
  justify-content: center;
  align-items: center;
  gap: 8px;
  min-width: 200px;
  padding: 12px 20px;
  font-size: 14px;
  display: inline-flex;
  box-shadow: 0 4px 16px #9b59b64d;
}

.toggle-button:hover {
  background: linear-gradient(135deg, #a569bd 0%, #9b59b6 100%);
  box-shadow: 0 8px 24px #9b59b666;
}

.toggle-icon {
  font-size: 12px;
  transition: transform .3s;
}

.toggle-button.collapsed .toggle-icon {
  transform: rotate(180deg);
}

.section-header {
  border-bottom: 1px solid #ffffff1a;
  margin-bottom: 1.5rem;
  padding-bottom: 1rem;
}

.section-toggle-button {
  background: linear-gradient(135deg, #9b59b6 0%, #8e44ad 100%);
  border-radius: 12px;
  justify-content: center;
  align-items: center;
  gap: 8px;
  width: 100%;
  min-width: 200px;
  padding: 12px 20px;
  font-size: 14px;
  display: inline-flex;
  box-shadow: 0 4px 16px #9b59b64d;
}

.section-toggle-button:hover {
  background: linear-gradient(135deg, #a569bd 0%, #9b59b6 100%);
  box-shadow: 0 8px 24px #9b59b666;
}

.section-toggle-button .toggle-icon {
  font-size: 12px;
  transition: transform .3s;
}

.section-toggle-button.collapsed .toggle-icon {
  transform: rotate(180deg);
}

.bottom-controls {
  flex-direction: column;
  gap: 15px;
  margin-top: 20px;
  display: flex;
}

.hamburger-menu {
  z-index: 1000;
  position: fixed;
  top: 15px;
  right: 15px;
}

.hamburger-button {
  cursor: pointer;
  background: linear-gradient(135deg, #555 0%, #444 100%);
  border: none;
  border-radius: 12px;
  justify-content: center;
  align-items: center;
  width: 54px;
  height: 54px;
  margin: 0;
  padding: 0;
  transition: all .3s;
  display: flex;
  box-shadow: 0 4px 16px #00000080;
}

.hamburger-button:hover {
  background: linear-gradient(135deg, #666 0%, #555 100%);
  transform: translateY(-2px);
  box-shadow: 0 8px 24px #0009;
}

.hamburger-icon {
  flex-direction: column;
  justify-content: space-between;
  width: 22px;
  height: 16px;
  display: flex;
}

.hamburger-line {
  background: #f39c12;
  border-radius: 2px;
  width: 100%;
  height: 2.5px;
  transition: all .3s;
}

.hamburger-button.active .hamburger-line:first-child {
  transform: rotate(45deg)translate(5px, 5px);
}

.hamburger-button.active .hamburger-line:nth-child(2) {
  opacity: 0;
}

.hamburger-button.active .hamburger-line:nth-child(3) {
  transform: rotate(-45deg)translate(7px, -6px);
}

.menu-dropdown {
  opacity: 0;
  visibility: hidden;
  background: linear-gradient(135deg, #1e1e1e 0%, #2a2a2a 100%);
  border: 1px solid #ffffff26;
  border-radius: 12px;
  min-width: 240px;
  max-width: 280px;
  transition: all .3s;
  position: absolute;
  top: 64px;
  right: 0;
  overflow: hidden;
  transform: translateY(-10px);
  box-shadow: 0 8px 32px #0009;
}

.menu-dropdown.active {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}

.menu-item {
  cursor: pointer;
  border-bottom: 1px solid #ffffff1a;
  justify-content: space-between;
  align-items: center;
  min-height: 52px;
  padding: 16px 20px;
  transition: background .2s;
  display: flex;
}

.menu-item:last-child {
  border-bottom: none;
}

.menu-item:hover {
  background: #ffffff14;
}

.menu-item:active {
  background: #ffffff1f;
}

.menu-item-text {
  color: var(--text-primary);
  user-select: none;
  font-size: 15px;
  font-weight: 500;
}

.menu-toggle {
  cursor: pointer;
  background: #555;
  border-radius: 10px;
  width: 40px;
  height: 20px;
  transition: background .3s;
  position: relative;
}

.menu-toggle.active {
  background: #f39c12;
}

.menu-toggle-slider {
  background: #fff;
  border-radius: 50%;
  width: 16px;
  height: 16px;
  transition: transform .3s;
  position: absolute;
  top: 2px;
  left: 2px;
}

.menu-toggle.active .menu-toggle-slider {
  transform: translateX(20px);
}

.metric-info-modal {
  z-index: 10000;
  opacity: 0;
  visibility: hidden;
  backdrop-filter: blur(5px);
  background: #000000d9;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  transition: all .3s;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.metric-info-modal.active {
  opacity: 1;
  visibility: visible;
}

.metric-info-content {
  background: linear-gradient(135deg, #2a2a2a 0%, #1e1e1e 100%);
  border: 1px solid #ffffff26;
  border-radius: 20px;
  width: 90%;
  max-width: 500px;
  max-height: 80vh;
  padding: 0;
  transition: transform .3s;
  overflow: hidden;
  transform: scale(.9);
  box-shadow: 0 20px 60px #00000080;
}

.metric-info-modal.active .metric-info-content {
  transform: scale(1);
}

.metric-info-header {
  background: linear-gradient(135deg, #1e1e1e 0%, #2a2a2a 100%);
  border-bottom: 1px solid #ffffff1a;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem 2rem;
  display: flex;
}

.metric-info-header h3 {
  color: var(--accent-power);
  margin: 0;
  font-size: 1.5rem;
  font-weight: 600;
}

.metric-info-close {
  color: var(--text-primary);
  cursor: pointer;
  background: none;
  border: none;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  margin: 0;
  padding: 0;
  font-size: 2rem;
  line-height: 1;
  transition: all .2s;
  display: flex;
}

.metric-info-close:hover {
  background: #ffffff1a;
  transform: rotate(90deg);
}

.metric-info-body {
  max-height: calc(80vh - 80px);
  padding: 2rem;
  overflow-y: auto;
}

.metric-info-body p {
  color: var(--text-primary);
  margin: 0;
  font-size: 1rem;
  line-height: 1.6;
}

.export-modal {
  z-index: 10000;
  opacity: 0;
  visibility: hidden;
  background: #000c;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  transition: all .3s;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.export-modal.show {
  opacity: 1;
  visibility: visible;
}

.export-modal-content {
  background: linear-gradient(135deg, #2a2a2a 0%, #1e1e1e 100%);
  border: 1px solid #ffffff1a;
  border-radius: 16px;
  width: 90%;
  max-width: 500px;
  max-height: 80vh;
  padding: 2rem;
  transition: transform .3s;
  overflow-y: auto;
  transform: scale(.8);
  box-shadow: 0 20px 60px #00000080;
}

.export-modal.show .export-modal-content {
  transform: scale(1);
}

.export-modal-header {
  border-bottom: 1px solid #ffffff1a;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
  padding-bottom: 1rem;
  display: flex;
}

.export-modal-title {
  color: var(--accent-power);
  margin: 0;
  font-size: 1.5rem;
  font-weight: 600;
}

.export-modal-close {
  color: var(--text-secondary);
  cursor: pointer;
  background: none;
  border: none;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  margin: 0;
  padding: 0;
  font-size: 1.5rem;
  transition: color .3s;
  display: flex;
}

.export-modal-close:hover {
  color: var(--text-primary);
}

.export-modal-buttons {
  gap: .75rem;
  margin: 1rem 0;
  display: grid;
}

.export-modal-button {
  color: #fff;
  cursor: pointer;
  text-align: left;
  background: linear-gradient(135deg, #3498db 0%, #2980b9 100%);
  border: none;
  border-radius: 8px;
  align-items: center;
  gap: .5rem;
  padding: .75rem 1rem;
  font-size: .9rem;
